@import '../scss/variables';

body.dark-mode {
  color: #dedefd;
  background-color: #22223d;
}
body.dark-mode *::-webkit-scrollbar-thumb,
body.dark-mode *:hover::-webkit-scrollbar-thumb {
  background: #272740;
}
.dark-mode {
  caption {
    color: #505662;
  }
  .text-muted {
    color: #77778e !important;
  }
  hr {
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }
  .list-unstyled li {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .blockquote-footer {
    color: #77778e;
  }
  .img-thumbnail {
    background-color: #30304d;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .figure-caption {
    color: #77778e;
  }
  kbd {
    color: #fff;
    background-color: #343a40;
  }
  pre {
    color: #83839e;
  }
  .bg-transparent {
    background-color: transparent !important;
  }
  .border {
    border: 1px solid rgba(255, 255, 255, 0.1) !important;
  }
  .border-top {
    border-top: 1px solid rgba(255, 255, 255, 0.1) !important;
  }
  .border-right {
    border-right: 1px solid rgba(255, 255, 255, 0.1) !important;
  }
  .border-bottom {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
  }
  .border-left {
    border-left: 1px solid rgba(255, 255, 255, 0.1) !important;
  }
  .shadow-sm {
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
  }
  .shadow {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
  }
  .shadow-lg {
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175) !important;
  }
  blockquote {
    color: #77778e;
    border-left: 2px solid rgba(255, 255, 255, 0.1);
  }
  code {
    background: transparent;
    border: 1px solid transparent;
  }
  pre {
    color: #77778e;
    background-color: #2e2e4a;
    text-shadow: 0 1px #2e2e4a;
  }
  .section-nav {
    background-color: #f8f9fa;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .border {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .table thead th,
  .text-wrap table thead th {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .table tbody + tbody,
  .text-wrap table tbody + tbody {
    border-top: 2px solid rgba(255, 255, 255, 0.1);
  }
  .table .table,
  .text-wrap table .table,
  .table .text-wrap table {
    background-color: #2e2e4a;
  }
  .text-wrap {
    .table table,
    table table {
      background-color: #2e2e4a;
    }
  }
  .table-bordered {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  #external-events {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .fc-theme-standard td,
  .fc-theme-standard th {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .fc-theme-standard .fc-scrollgrid {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .fc .fc-daygrid-day-number {
    color: #dedefd;
  }
  .fc .fc-daygrid-day.fc-day-today {
    background-color: #2f2f46;
  }
  .fc-theme-standard .fc-list-day-cushion {
    background-color: #2f2f46;
  }
  .fc-theme-standard .fc-list {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .fc .fc-list-event:hover td {
    background-color: transparent;
    color: #6c5ffc;
  }
  .fc .fc-list-empty {
    background-color: #30304d;
  }
  .badge.bg-white {
    background-color: $white !important;
  }
}
@media print {
  .dark-mode {
    .table td,
    .text-wrap table td,
    .table th,
    .text-wrap table th {
      background-color: #fff !important;
    }
    .table-bordered th,
    .text-wrap table th,
    .table-bordered td,
    .text-wrap table td {
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
  }
}
.text-wrap table {
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.dark-mode {
  .table-bordered th,
  .text-wrap table th,
  .table-bordered td,
  .text-wrap table td {
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .table-striped tbody tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.1);
    color: inherit;
  }
  .table-hover tbody {
    tr:hover,
    th {
      background-color: #2e2e4a;
    }
  }
  .table-active {
    background-color: rgba(0, 0, 0, 0.04);
    > {
      th,
      td {
        background-color: rgba(0, 0, 0, 0.04);
      }
    }
  }
  .table-hover .table-active:hover {
    background-color: rgba(0, 0, 0, 0.04);
    > {
      td,
      th {
        background-color: rgba(0, 0, 0, 0.04);
      }
    }
  }
  .table-dark {
    &.table-striped tbody tr:nth-of-type(odd) {
      background-color: rgba(255, 255, 255, 0.05);
    }
    &.table-hover tbody tr:hover {
      background-color: rgba(255, 255, 255, 0.075);
    }
  }
  .table.dataTable thead th {
    border-bottom: 0 !important;
  }
  .form-control {
    color: #dedefd !important;
    background-color: #2e2e4a;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .authentication {
    .card:hover {
      box-shadow: 0 16px 26px 0 rgba(0, 0, 0, 0.4),
        0 3px 6px 0 rgba(0, 0, 0, 0.4);
    }
    input::placeholder {
      color: #77778e;
    }
    .flex-c-m .login100-social-item i {
      color: #fff;
    }
  }
  .login100-social-item {
    color: #fff;
    &:hover {
      color: #fff;
    }
  }
  .select2-container--default {
    .select2-selection--single {
      background-color: #2e2e4a;
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
    &.select2-container--focus .select2-selection--multiple {
      background-color: #2e2e4a;
      border: 1px solid rgba(255, 255, 255, 0.1);
    }
    .select2-selection--multiple {
      background-color: #2e2e4a;
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
    .select2-search--dropdown .select2-search__field {
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
      background: #2e2e4a;
    }
    .select2-selection--multiple {
      .select2-selection__choice,
      .select2-selection__choice__remove {
        color: #fff !important;
      }
    }
    .select2-results > .select2-results__options {
      box-shadow: 0px 8px 14.72px 1.28px #2a2635;
    }
  }
  .form-control {
    &:focus {
      background-color: #2e2e4a;
      border: 1px solid rgba(255, 255, 255, 0.1) !important;
    }
    &::-webkit-input-placeholder,
    &::-moz-placeholder,
    &:-ms-input-placeholder,
    &::-ms-input-placeholder {
      color: #9999bf;
      opacity: 0.6;
    }
    &::placeholder {
      color: #9999bf;
      opacity: 0.6;
    }
    color: #99c1b2;
    opacity: 1;
    &:disabled,
    &[readonly] {
      background-color: #2d2d48;
    }
  }
  .select2-container--default
    .select2-selection--single
    .select2-selection__placeholder {
    color: #9999bf;
    opacity: 0.6;
  }
  .select2-container--default .select2-selection--single {
    .select2-selection__rendered {
      color: #77778e;
    }
    .select2-selection__arrow {
      height: 2.375rem !important;
    }
  }
  select.form-control:focus::-ms-value {
    color: #77778e;
    background-color: #fff;
    border: 1px solid rgba(255, 255, 255, 0.1);
  }
  .form-control-file {
    display: block;
    width: 100%;
  }
}
@media (max-width: 575.98px) {
  .dark-mode .countdown-timer-wrapper .timer .timer-wrapper .time {
    color: #fff;
    background: linear-gradient(
      to bottom right,
      rgba(0, 0, 0, 0.1),
      rgba(0, 0, 0, 0.1)
    );
  }
}
.form-control-range {
  display: block;
  width: 100%;
}
.dark-mode {
  .form-control-plaintext {
    color: #77778e;
  }
  .form-check-input:disabled ~ .form-check-label {
    color: #505662;
  }
  .valid-feedback {
    color: #09ad95;
  }
  .valid-tooltip {
    color: #fff;
    background-color: rgba(94, 186, 0, 0.8);
  }
  .was-validated .form-control:valid,
  .form-control.is-valid,
  .was-validated .custom-select:valid,
  .custom-select.is-valid {
    border-color: #09ad95;
  }
  .was-validated .form-control:valid:focus,
  .form-control.is-valid:focus,
  .was-validated .custom-select:valid:focus,
  .custom-select.is-valid:focus {
    border-color: #09ad95;
    box-shadow: 0 0 0 2px rgba(94, 186, 0, 0.25);
  }
  .was-validated .form-check-input:valid ~ .form-check-label,
  .form-check-input.is-valid ~ .form-check-label,
  .was-validated .custom-control-input:valid ~ .custom-control-label,
  .custom-control-input.is-valid ~ .custom-control-label {
    color: #09ad95;
  }
  .was-validated .custom-control-input:valid ~ .custom-control-label::before,
  .custom-control-input.is-valid ~ .custom-control-label::before {
    background-color: #09ad95;
  }
  .was-validated
    .custom-control-input:valid:checked
    ~ .custom-control-label::before,
  .custom-control-input.is-valid:checked ~ .custom-control-label::before {
    background-color: #09ad95;
  }
  .was-validated
    .custom-control-input:valid:focus
    ~ .custom-control-label::before,
  .custom-control-input.is-valid:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #2e2e4a, 0 0 0 2px rgba(94, 186, 0, 0.25);
  }
  .was-validated .form-file-input:valid ~ .form-file-label,
  .form-file-input.is-valid ~ .form-file-label {
    border-color: #09ad95;
  }
  .was-validated .form-file-input:valid:focus ~ .form-file-label,
  .form-file-input.is-valid:focus ~ .form-file-label {
    box-shadow: 0 0 0 2px rgba(94, 186, 0, 0.25);
  }
  .invalid-feedback {
    color: $danger;
  }
  .invalid-tooltip {
    color: #fff;
    background-color: rgba(205, 32, 31, 0.8);
  }
  .was-validated .form-control:invalid {
    border-color: $danger;
  }
}
.form-control.is-invalid {
  border-color: $danger;
}
.dark-mode {
  .was-validated .custom-select:invalid,
  .custom-select.is-invalid {
    border-color: $danger;
  }
  .was-validated .form-control:invalid:focus,
  .form-control.is-invalid:focus,
  .was-validated .custom-select:invalid:focus,
  .custom-select.is-invalid:focus {
    border-color: $danger;
    box-shadow: 0 0 0 2px rgba(205, 32, 31, 0.25);
  }
  .was-validated .form-check-input:invalid ~ .form-check-label,
  .form-check-input.is-invalid ~ .form-check-label,
  .was-validated .custom-control-input:invalid ~ .custom-control-label,
  .custom-control-input.is-invalid ~ .custom-control-label {
    color: $danger !important;
  }
  .was-validated .custom-control-input:invalid ~ .custom-control-label::before,
  .custom-control-input.is-invalid ~ .custom-control-label::before {
    background-color: $danger;
    border-color: #e62b4a;
  }
  .was-validated
    .custom-control-input:invalid:checked
    ~ .custom-control-label::before,
  .custom-control-input.is-invalid:checked ~ .custom-control-label::before {
    background-color: #e23e3d;
  }
  .was-validated
    .custom-control-input:invalid:focus
    ~ .custom-control-label::before,
  .custom-control-input.is-invalid:focus ~ .custom-control-label::before {
    box-shadow: 0 0 0 1px #2e2e4a, 0 0 0 2px rgba(205, 32, 31, 0.25);
  }
  .was-validated .form-file-input:invalid ~ .form-file-label,
  .form-file-input.is-invalid ~ .form-file-label {
    border-color: $danger;
  }
  .was-validated .form-file-input:invalid:focus ~ .form-file-label,
  .form-file-input.is-invalid:focus ~ .form-file-label {
    box-shadow: 0 0 0 2px rgba(205, 32, 31, 0.25);
  }
